import styled, { css } from 'styled-components'

const wrapperModifiers = {
  left: css`
    left: 5px;
  `,
  right: css`
    left: auto;
    right: 5px;
  `
}

type WrapperProps = {
  isDisabled: boolean
  isLeft: boolean
}

export const Wrapper = styled.svg<WrapperProps>`
  ${({ isDisabled, isLeft }) => css`
    width: 30px;
    height: 30px;
    position: absolute;
    top: 66%;
    transform: translateY(-50%);
    fill: ${isDisabled ? 'rgba(0, 0, 0, 0.2)' : 'rgba(0, 0, 0, 0.5)'};
    cursor: pointer;
    z-index: 3;

    ${isLeft ? wrapperModifiers.left : wrapperModifiers.right}
  `}
`
