import styled, { css } from 'styled-components'

type WrapperProps = {
  maxWidth: string
}

export const Wrapper = styled.div<WrapperProps>`
  ${({ maxWidth }) => css`
    max-width: ${maxWidth && maxWidth};
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;

    & * {
      transition: all 0.2s ease-in-out;
    }
  `}
`

type InputProps = {
  isFocus?: boolean
  sizeInput?: 'small' | 'medium' | 'x-small'
  disabled?: boolean
  themeColor?: 'normal' | 'error'
}

const disabledModifiers = {
  default: css`
    & ~ label {
      border-bottom-right-radius: 3px;
      border-bottom-left-radius: 3px;
    }
  `
}

const inputModifiers = {
  medium: css`
    padding: 1.1rem 1.3rem;
    font-size: 1.4rem;

    &::placeholder {
      font-size: 1.2rem;
      font-weight: 100;
    }

    & ~ label {
      left: 14px;
      top: 14px;
    }
  `,
  small: css`
    padding: 0.9rem 1.1rem;
    font-size: 1.2rem;

    &::placeholder {
      font-size: 1.2rem;
      font-weight: 300;
    }

    & ~ label {
      left: 12px;
      top: 10px;
    }
  `,
  'x-small': css`
    padding: 0.5rem 0.7rem;
    font-size: 1.1rem;

    &::placeholder {
      font-size: 1.1rem;
      font-weight: 300;
    }

    & ~ label {
      font-size: 1.1rem !important;
      left: 8px;
      top: 6px;
    }
  `
}

const themeColorModifiers = {
  normal: css`
    &:focus ~ label {
      color: #1273eb;
    }

    &:focus {
      color: #000000;
      border: 1px solid #1273eb;
    }
  `,
  error: css`
    color: #eb1212;
    background: rgba(255, 0, 0, 0.04);
    border: 1px solid #eb1212;

    &:focus ~ label {
      color: #eb1212;
    }

    &:focus {
      color: #000000;
      border: 1px solid #eb1212;
    }
  `
}

export const Input = styled.input<InputProps>`
  ${({
    isFocus = false,
    sizeInput = 'medium',
    disabled = false,
    themeColor = 'normal'
  }) => css`
    color: #797979;
    background: ${disabled ? '#efefef' : 'rgba(0, 0, 0, 0)'};
    width: 100%;
    font-weight: 500;
    border: 1px solid #dadce0;
    position: relative;
    border-radius: 4px;
    z-index: 1;

    ${inputModifiers[sizeInput]}
    ${disabled && isFocus && disabledModifiers.default}

    &::placeholder {
      color: #a3a3a3;
    }

    &:focus ~ label${isFocus && ', & ~ label'} {
      color: #b7b7b7;
      background: #fff;
      padding: 0 8px;
      z-index: 1;
      top: -9px;
    }

    ${themeColorModifiers[themeColor]}
  `}
`

export const Label = styled.label`
  font-weight: 300;
  position: absolute;
  font-size: 1.2rem;
`

export const Line = styled.div`
  color: #797979;
  position: absolute;
  z-index: 3;
  top: -7px;
  right: 1rem;
  background: #fff;
  padding: 0 0.8rem;
  display: flex;
`

export const Text = styled.span`
  color: #797979;
  text-transform: uppercase;
  font-family: var(--font-open-sans);
  font-weight: 600;
  font-size: 1rem;
  line-height: 1.6rem;
  margin: 0 0.2rem;
`

export const Parenthesis = styled.span`
  color: #797979;
  font-weight: 600;
  font-size: 1rem;
  font-family: var(--font-open-sans);
`
