// contexts and others
import { ValuesContext } from '../../../contexts/DataInvite'

// components JSX
import Switch from '../../../components/Switch'

// styles
import * as S from './styles'

// types and interfaces
import { CardPersonProps } from './types'

const CardPerson = ({ id, name, isPresence }: CardPersonProps) => {
  const { values, setValues } = ValuesContext()

  const handleCheckbox = () => {
    const current = { ...values }
    const individuals = current.individuals
    const getIndex = individuals.findIndex((data) => data.id === id)
    const isPresence = current.individuals[getIndex].isPresence

    current.individuals[getIndex].isPresence = !isPresence

    setValues(current)
  }

  return (
    <S.Wrapper>
      <S.Name>{name}</S.Name>
      <Switch checked={isPresence} onClick={handleCheckbox} />
    </S.Wrapper>
  )
}

export default CardPerson
