import { useAsync } from 'react-async'

// contexts and others
import { IdContext } from '../../contexts/Invite'
import api from '../../app/axios'

// components JSX
import Data from './data'
import Disabled from './error'
import Skeleton from './skeleton'

// types and interfaces
import type { PromiseFn } from 'react-async'
import type { ErrorResponse, TInvite } from './types'

const fetchPerson: PromiseFn<TInvite> = async ({ id }) => {
  const fetchUrl = `invite?id=${id}`
  return api.get(fetchUrl).then((data) => data.data)
}

const FetchProductId = () => {
  const { id } = IdContext()

  const { data, error } = useAsync({
    promiseFn: fetchPerson,
    id
  })

  const __error = error as unknown as ErrorResponse
  const message = __error?.response?.data.message
  const isDisabled = message === 'INVITATION_DEACTIVATED'

  if (isDisabled) return <Disabled />
  if (error) return <div>{error.message}</div>
  if (data) return <Data {...{ data }} />
  return <Skeleton />
}

export default FetchProductId
