import { useState } from 'react'
import { useKeenSlider } from 'keen-slider/react'

// contexts and others
import InviteProvider from '../../contexts/DataInvite'

// components JSX
import ModalDefault from '../../components/Modals/Default'
import ModalSubmit from '../../components/Modals/Submit'
import Arrow from '../../components/Arrow'
import Primary from '../../templates/Primary'
import Secondary from '../../templates/Secondary'
import Tertiary from '../../templates/Tertiary'

// styles
import * as S from './styles'

// types and interfaces
import type { InviteProps } from './types'
import { TSubmitModal } from '../../typings/Contexts'

const DataInvite = ({ data }: InviteProps) => {
  const [values, setValues] = useState(data)
  const [submitModal, setSubmitModal] = useState<TSubmitModal>({})

  const [currentSlide, setCurrentSlide] = useState(0)
  const [loaded, setLoaded] = useState(false)
  const [sliderRef, instanceRef] = useKeenSlider<HTMLDivElement>({
    initial: 0,
    slides: {
      spacing: 16
    },
    slideChanged(slider) {
      setCurrentSlide(slider.track.details.rel)
    },
    created() {
      setLoaded(true)
    },
    vertical: true
  })

  return (
    <InviteProvider
      {...{
        values,
        setValues,
        submitModal,
        setSubmitModal,
        instanceRef
      }}
    >
      <S.Wrapper>
        <div ref={sliderRef} className="keen-slider">
          <S.Content className="keen-slider__slide">
            <Primary />
          </S.Content>
          <S.Content className="keen-slider__slide">
            <Secondary />
          </S.Content>
          <S.Content className="keen-slider__slide">
            <Tertiary />
          </S.Content>
        </div>
      </S.Wrapper>

      {loaded && instanceRef.current && (
        <>
          <Arrow
            left
            onClick={(e) => e.stopPropagation() || instanceRef.current?.prev()}
            disabled={currentSlide === 0}
          />

          <Arrow
            onClick={(e) => e.stopPropagation() || instanceRef.current?.next()}
            disabled={
              currentSlide ===
              instanceRef.current.track.details.slides.length - 1
            }
          />
        </>
      )}

      <ModalDefault state={submitModal} setState={setSubmitModal}>
        <ModalSubmit />
      </ModalDefault>
    </InviteProvider>
  )
}

export default DataInvite
