// styles
import * as S from './styles'

// types and interfaces
import { SwitchProps } from './types'

const Switch = ({ checked, label, onClick }: SwitchProps) => {
  return (
    <S.Wrapper>
      <S.Status type="checkbox" readOnly checked={checked} />
      <S.Label useMargin={!!label} {...(onClick ? { onClick } : {})}>
        {label}
      </S.Label>
    </S.Wrapper>
  )
}

export default Switch
