import { useLocation } from 'react-router-dom'

// contexts and others
import InviteProvider from '../../contexts/Invite'

// components JSX
import DataFetch from './fetch'

// types and interfaces
// import { UseParamsProps } from './types'

const Invite = () => {
  const { search } = useLocation()

  const query = new URLSearchParams(search)
  const id = query.get('invite')

  if (!id) return <div>Cdigo invlido!</div>

  return (
    <InviteProvider {...{ id }}>
      <DataFetch />
    </InviteProvider>
  )
}

export default Invite
