import styled, { css } from 'styled-components'

// utils
import { getImage } from '../../utils/tools'

type WrapperProps = {
  urlFile: string
}

export const Wrapper = styled.div<WrapperProps>`
  ${({ urlFile }) => css`
    height: 100%;
    display: block;
    position: relative;
    overflow: hidden;
    width: 100%;

    &::before {
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      background: linear-gradient(
        to top,
        rgba(0, 0, 0, 0.4) 0%,
        rgba(0, 0, 0, 0) 57%
      );
      z-index: 1;
    }

    &::after {
      content: '';
      width: calc(100vw - 32px);
      max-width: 100%;
      background-image: url('${getImage(urlFile)}');
      display: block;
      position: absolute;
      height: 100%;
      left: 50%;
      transform: translate(-50%, 0);
      background-size: cover;
      background-repeat: no-repeat;
      background-position-x: center;
      background-position-y: 0;
    }
  `}
`
