import { useState, useRef } from 'react'
import { DefaultLayout } from 'react-photoswipe-gallery'

// contexts and others
import AppProvider from '../contexts/App'
import OverloadProvider from '../contexts/Overload'

// components JSX
import Routers from '../app/routes'
import Overload from '../components/Overload'

// styles
import StyledToastContainer from '../styles/toastify'

// types and interfaces
import type { SOverload } from '../typings/Contexts'

// global css imports from lib
import 'react-toastify/dist/ReactToastify.css'
import 'react-loading-skeleton/dist/skeleton.css'
// import 'photoswipe/dist/photoswipe.css'
// import 'photoswipe/dist/default-skin/default-skin.css'

function App() {
  const layoutRef = useRef<HTMLElement>(null!)
  const [overload, setOverload] = useState<SOverload>({})

  return (
    <AppProvider {...{ layoutRef }}>
      <OverloadProvider {...{ overload, setOverload }}>
        <Routers />
        <Overload />
        <StyledToastContainer />
        <DefaultLayout
          shareButton={false}
          fullscreenButton={true}
          zoomButton={true}
          ref={layoutRef}
        />
      </OverloadProvider>
    </AppProvider>
  )
}

export default App
