// styles
import * as S from './styles'

// types and interfaces
import { InputProps } from './types'

const Input = ({
  label,
  value,
  size = 'medium',
  isFocus = false,
  ref,
  onChange,
  disabled = false,
  type,
  style,
  placeholder,
  onFocus,
  onBlur,
  themeColor,
  isOpcional
}: InputProps) => {
  const maxWidth = style?.maxWidth as string

  return (
    <S.Wrapper maxWidth={maxWidth}>
      <S.Input
        value={value}
        isFocus={isFocus}
        sizeInput={size}
        themeColor={themeColor}
        {...(ref ? { ref } : {})}
        {...(onChange ? { onChange } : { readOnly: true })}
        {...(disabled ? { disabled } : {})}
        {...(type ? { type } : {})}
        {...(style ? { style } : {})}
        {...(placeholder ? { placeholder } : {})}
        {...(onFocus ? { onFocus } : {})}
        {...(onBlur ? { onBlur } : {})}
      />
      {!!label && <S.Label>{label}</S.Label>}
      {isOpcional && (
        <S.Line>
          <S.Parenthesis>(</S.Parenthesis>
          <S.Text>opcional</S.Text>
          <S.Parenthesis>)</S.Parenthesis>
        </S.Line>
      )}
    </S.Wrapper>
  )
}

export default Input
