import { createContext, useContext } from 'react'

import { ProviderApp, ContextApp } from '../typings/Contexts'

const ElementContext = createContext<ContextApp>(null!)

export default function ElementProvider({ children, layoutRef }: ProviderApp) {
  return (
    <ElementContext.Provider value={{ layoutRef }}>
      {children}
    </ElementContext.Provider>
  )
}

export function SnapshotStatus() {
  const { layoutRef } = useContext(ElementContext)
  return { layoutRef }
}
