import { createContext, useContext } from 'react'

import { ContextInvite, ProviderInvite } from '../typings/Contexts'

const ElementContext = createContext<ContextInvite>(null!)

export default function ElementProvider({ children, id }: ProviderInvite) {
  return (
    <ElementContext.Provider value={{ id }}>{children}</ElementContext.Provider>
  )
}

export function IdContext() {
  const context = useContext(ElementContext)
  const { id } = context
  return { id }
}
