import styled, { css } from 'styled-components'

import { ButtonProps } from './types'

type ContentProps = {
  size: NonNullable<ButtonProps['size']>
  variant: NonNullable<ButtonProps['variant']>
  disabledColor?: boolean
  maxWidth?: string
  currentColor?: string
  isCustom: boolean
}

const modifiertsSize = {
  'x-small': (minWidth: string) => css`
    font-weight: 500;
    font-size: 1rem;
    min-width: ${minWidth};
    min-height: 26px;
  `,
  small: (minWidth: string) => css`
    font-size: 1.2rem;
    min-width: ${minWidth};
    min-height: 30px;
  `,
  normal: (minWidth: string) => css`
    font-size: 1.3rem;
    min-width: ${minWidth};
    min-height: 36px;
  `,
  large: (minWidth: string) => css`
    font-size: 1.4rem;
    min-width: ${minWidth};
    min-height: 40px;
  `
}

const modifiertsVariant = {
  normal: () => css`
    color: #000;

    &:hover {
      background-color: rgba(0, 0, 0, 0.08);
    }
  `,
  contained: (disabled: boolean) => css`
    color: ${disabled ? '#000' : '#fff'};
    background-color: ${disabled ? '#ccc' : 'var(--color-primary)'};
    font-weight: ${disabled && 500};

    &:hover {
      background: ${disabled ? '#bbb' : '#f599a1'};
    }

    &:active {
      background: ${disabled ? '#aaa' : '#d36e77'};
    }

    &:focus {
      box-shadow: ${disabled
        ? '0px 0px 4px 2px rgba(0, 0, 0, 0.05)'
        : `0px 0px 4px 2px rgba(255, 215, 219)`};
    }
  `,
  outlined: (disabled: boolean, currentColor?: string) => css`
    color: ${disabled ? '' : currentColor || 'var(--color-primary)'};

    border-color: ${disabled ? '' : currentColor || 'var(--color-primary)'};
    border-width: 1px;
    border-style: solid;

    &:hover {
      color: #fff;
      background: var(--color-primary);
    }

    &:active {
      color: #fff;
      background: #e96672;
    }
  `,
  'outlined-ripple': (disabled: boolean, currentColor?: string) => css`
    color: ${disabled ? '' : currentColor || 'var(--color-primary)'};

    border-color: ${disabled ? '' : currentColor || 'var(--color-primary)'};
    border-width: 1px;
    border-style: solid;

    overflow: hidden;
    position: relative;

    &:hover {
      background: #fff4f5;
    }
  `,
  simple: () => css`
    color: #000;
    font-weight: 500;
  `
}

const modifiersMaxWidth = {
  normal: (maxWidth: string) => css`
    width: 100%;
    max-width: ${maxWidth};
  `
}

export const Content = styled.button<ContentProps>`
  ${({
    theme,
    size,
    variant,
    disabledColor = false,
    maxWidth,
    isCustom,
    currentColor
  }) => css`
    font-family: ${theme.font.family};
    transition: all 0.15s linear;
    font-weight: 300;
    border-radius: 3px;
    padding: 0 1.2rem;

    ${modifiertsSize[size](maxWidth || '132px')}
    ${!isCustom && modifiertsVariant[variant](disabledColor, currentColor)}
    ${maxWidth && modifiersMaxWidth.normal(maxWidth)}
  `}
`

export const Main = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 1;

  & > *:not(:last-child) {
    margin-right: 1rem;
  }
`

export const Text = styled.span`
  font-weight: 500;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
`
