import { useRef } from 'react'
import { toast } from 'react-toastify'

// contexts and others
import { IdContext } from '../../../contexts/Invite'
import { ValuesContext } from '../../../contexts/DataInvite'
import { OverloadContext } from '../../../contexts/Overload'
import { CloseContext } from '../../../components/Modals/Default/context'

// components JSX
import Button from '../../../components/Button'
// import Notify from '../../../components/Notify'
import Input from '../../../components/Input'
import CardPerson from '../../../components/Cards/Person'

// styles
import * as S from './styles'
import api from '../../../app/axios'
import { TProperty } from './types'

const ModalSubmit = () => {
  const { id } = IdContext()
  const { close } = CloseContext()
  const { setOverload } = OverloadContext()
  const { values, setValues } = ValuesContext()

  const individuals = values.individuals

  const clickRef = useRef(true)
  const scrollRef = useRef<HTMLDivElement>(null!)

  const updateInvite = async () => {
    const fetchUrl = `invite?id=${id}`
    return api.put(fetchUrl, values)
  }

  const presenceKeys = Array.from(individuals, ({ isPresence }) => isPresence)
  const validationKeys = [!!values.phoneNumber, presenceKeys.includes(true)]
  const validationValue = validationKeys.every(Boolean)
  const dataLength = presenceKeys.filter(Boolean).length

  const handleSubmit = () => {
    if (clickRef.current) {
      if (validationValue) {
        clickRef.current = false
        setOverload({ status: true })

        updateInvite()
          .then(() => {
            toast.success('Publicado com sucesso!')
            close()
          })
          .catch(() => {
            toast.error('Falha!')
          })
          .finally(() => {
            clickRef.current = true
            setOverload({ status: false })
          })
      } else {
        if (!dataLength) {
          toast.warn('Nenhuma presena marcada')
        } else if (!values.phoneNumber) {
          toast.warn('O nmero de telefone  obrigatrio')
        }
      }
    }
  }

  const inputFn = (value: string, property: TProperty) => {
    setValues((prevState) => ({
      ...prevState,
      [property]: value
    }))
  }

  return (
    <S.Wrapper>
      <S.Header>
        <S.Title>Confirme a presena abaixo!</S.Title>
      </S.Header>
      <S.Content>
        <S.Scroll ref={scrollRef}>
          <S.Main>
            {Array.from(individuals, (props) => (
              <CardPerson key={props.id} {...props} />
            ))}
          </S.Main>
        </S.Scroll>
      </S.Content>
      {/* <Notify /> */}
      <S.Form>
        <S.Field>
          <Input
            label="Telefone"
            placeholder="Digite seu telefone"
            size="small"
            value={values.phoneNumber ?? ''}
            onChange={(e) => inputFn(e.currentTarget.value, 'phoneNumber')}
            isFocus
          />
        </S.Field>
        <S.Field>
          <Input
            label="E-mail"
            placeholder="Informe seu e-mail"
            size="small"
            value={values.email ?? ''}
            onChange={(e) => inputFn(e.currentTarget.value, 'email')}
            isFocus
            isOpcional
          />
        </S.Field>
      </S.Form>
      <S.Footer>
        <div></div>
        <Button
          variant="contained"
          value="Enviar"
          onClick={() => handleSubmit()}
          size="x-small"
          disabled={!validationValue}
        />
      </S.Footer>
    </S.Wrapper>
  )
}

export default ModalSubmit
