import { toast } from 'react-toastify'

// icons JSX
import { Place as IconPlace } from '@styled-icons/material/Place'
import { Waze as IconWaze } from '@styled-icons/simple-icons/Waze'
import { Uber as IconUber } from '@styled-icons/fa-brands/Uber'
import { Googlemaps as IconGooglemaps } from '@styled-icons/simple-icons/Googlemaps'
import { Whatsapp as IconWhatsapp } from '@styled-icons/simple-icons/Whatsapp'
import { DirectionsCar as IconDirectionsCar } from '@styled-icons/material/DirectionsCar'
import { ContentCopy as IconContentCopy } from '@styled-icons/material/ContentCopy'
import { OpenInNew as IconOpenInNew } from '@styled-icons/material/OpenInNew'

// styles
import * as S from './styles'

const Tertiary = () => {
  const handleOpenInWaze = () => {
    const latitude = '-23.611253755486366'
    const longitude = '-46.40557809814799'
    const addressEncode = `https://www.waze.com/ul?ll=${latitude}%2C${longitude}&navigate=yes&zoom=17`
    window.open(addressEncode, '_system')
  }

  const handleOpenInUber = () => {
    const name = 'Manso Esteves'
    const nameFormated = encodeURI(name)
    const latitude = '-23.611253755486366'
    const longitude = '-46.40557809814799'
    const addressEncode = `https://m.uber.com/ul/?action=setPickup&pickup=my_location&dropoff[formatted_address]=${nameFormated}&dropoff[latitude]=${latitude}&dropoff[longitude]=${longitude}`
    window.open(addressEncode, '_system')
  }

  const handleOpenInMaps = () => {
    const address = 'Estrada Vov Carolina, 2540 - Jardim Palanque'
    const addressFormated = encodeURI(address)
    const addressEncode = `https://www.google.com/maps/search/${addressFormated}`
    window.open(addressEncode, '_system')
  }

  const text =
    'Estr. Vov Carolina, 2540 - Cidade Tiradentes, So Paulo - SP, 08473-370'
  const handleCopyAddress = () => {
    if (window.clipboardData && window.clipboardData.setData) {
      toast.success('Copiado com sucesso!')
      // IE specific code path to prevent textarea being shown while dialog is visible.
      return window.clipboardData.setData('Text', text)
    } else if (
      document.queryCommandSupported &&
      document.queryCommandSupported('copy')
    ) {
      const textarea = document.createElement('textarea')
      textarea.textContent = text
      textarea.style.position = 'fixed' // Prevent scrolling to bottom of page in MS Edge.
      document.body.appendChild(textarea)
      textarea.select()
      try {
        toast.success('Copiado com sucesso!')
        return document.execCommand('copy') // Security exception may be thrown by some browsers.
      } catch (ex) {
        console.warn('Copy to clipboard failed.', ex)
        return false
      } finally {
        document.body.removeChild(textarea)
      }
    }
  }

  const handleOpenWhatsAppBruna = () => {
    const fetchUrl = `https://api.whatsapp.com/send?phone=5511940709524`
    window.open(fetchUrl)
  }

  const handleOpenWhatsAppNatanael = () => {
    const fetchUrl = `https://api.whatsapp.com/send?phone=554991694154`
    window.open(fetchUrl)
  }

  return (
    <S.Wrapper>
      <S.Cover></S.Cover>
      <S.Content>
        <S.Head>
          <S.Title>Manso Esteves</S.Title>
          <S.Description>Local do evento</S.Description>
        </S.Head>
        <S.Main>
          <S.Buttons>
            <S.Button onClick={() => handleOpenInWaze()}>
              <IconWaze size={32} />
              <S.ButtonName>Waze</S.ButtonName>
            </S.Button>
            <S.Button onClick={() => handleOpenInUber()}>
              <IconUber size={32} />
              <S.ButtonName>Uber</S.ButtonName>
            </S.Button>
            <S.Button onClick={() => handleOpenInMaps()}>
              <IconGooglemaps size={32} />
              <S.ButtonName>Maps</S.ButtonName>
            </S.Button>
          </S.Buttons>
          <S.Collection>
            <S.Item onClick={() => handleCopyAddress()}>
              <S.Icon>
                <IconPlace size={24} />
              </S.Icon>
              <S.Text>{text}</S.Text>
              <S.Float data-controls>
                <S.FloatContent>
                  <IconContentCopy size={18} />
                </S.FloatContent>
              </S.Float>
            </S.Item>
            <S.Item>
              <S.Icon>
                <IconDirectionsCar size={24} />
              </S.Icon>
              <S.Text>40 vagas no estacionamento</S.Text>
            </S.Item>
          </S.Collection>
          <S.HeadCol>
            <S.HeadColText>
              Precisando de ajuda, nos chame no WhatsApp.
            </S.HeadColText>
          </S.HeadCol>
          <S.Collection>
            <S.Item onClick={() => handleOpenWhatsAppBruna()}>
              <S.Icon>
                <IconWhatsapp size={20} />
              </S.Icon>
              <S.Text>Falar com Bruna</S.Text>
              <S.Float data-controls>
                <S.FloatContent>
                  <IconOpenInNew size={18} />
                </S.FloatContent>
              </S.Float>
            </S.Item>
            <S.Item onClick={() => handleOpenWhatsAppNatanael()}>
              <S.Icon>
                <IconWhatsapp size={20} />
              </S.Icon>
              <S.Text>Falar com Natanael</S.Text>
              <S.Float data-controls>
                <S.FloatContent>
                  <IconOpenInNew size={18} />
                </S.FloatContent>
              </S.Float>
            </S.Item>
          </S.Collection>
        </S.Main>
      </S.Content>
    </S.Wrapper>
  )
}

export default Tertiary
