import { useRef } from 'react'

// styles
import * as S from './styles'

// types and interfaces
import { ButtonProps } from './types'

const Button = ({
  children,
  value,
  type = 'button',
  size = 'normal',
  variant = 'normal',
  onClick,
  disabled = false,
  maxWidth,
  customColorStyled,
  currentColor
}: ButtonProps) => {
  const buttonRef = useRef<HTMLButtonElement>(null!)

  const handleOnClick = (e: React.MouseEvent<HTMLElement>) => {
    onClick && onClick()
    if (variant === 'outlined-ripple') {
      const button = buttonRef.current
      const currentTarget = document.createElement('div')
      currentTarget.classList.add('ripple__effect')

      const d = Math.max(button.clientWidth, button.clientHeight)

      currentTarget.style.width = currentTarget.style.height = d + 'px'

      const rect = button.getBoundingClientRect()
      currentTarget.style.left = e.clientX - rect.left - d / 2 + 'px'
      currentTarget.style.top = e.clientY - rect.top - d / 2 + 'px'

      button.appendChild(currentTarget)

      setTimeout(() => currentTarget.remove(), 700)
    }
  }

  return (
    <S.Content
      ref={buttonRef}
      type={type}
      size={size}
      variant={variant}
      disabledColor={disabled}
      onClick={(event: React.MouseEvent<HTMLElement>) => {
        handleOnClick(event)
      }}
      maxWidth={maxWidth}
      {...(customColorStyled ? { as: customColorStyled } : {})}
      isCustom={!!customColorStyled}
      currentColor={currentColor}
    >
      <S.Main>
        {!!children && children}
        <S.Text>{value}</S.Text>
      </S.Main>
    </S.Content>
  )
}

export default Button
