import { createContext, useContext } from 'react'

import { ContextDataInvite, ProviderDataInvite } from '../typings/Contexts'

const ElementContext = createContext<ContextDataInvite>(null!)

export default function ElementProvider({
  children,
  values,
  setValues,
  submitModal,
  setSubmitModal,
  instanceRef
}: ProviderDataInvite) {
  return (
    <ElementContext.Provider
      value={{
        values,
        setValues,
        submitModal,
        setSubmitModal,
        instanceRef
      }}
    >
      {children}
    </ElementContext.Provider>
  )
}

export function ValuesContext() {
  const context = useContext(ElementContext)
  const { values, setValues } = context
  return { values, setValues }
}

export function SubmitModalContext() {
  const context = useContext(ElementContext)
  const { submitModal, setSubmitModal } = context
  return { submitModal, setSubmitModal }
}

export function InstanceRefContext() {
  const context = useContext(ElementContext)
  const { instanceRef } = context
  return { instanceRef }
}
